<template>
  <div class="warps">
    <my-el-header />
    <div class="container">
      <el-row class="main">
        <div class="container-l">
          <e-tree @treeClick="treeClick" :data="treeArr" />
        </div>
        <div class="container-r">
          <div class="container-nav">
            <div class="container-form">
              <el-form :inline="true" ref="form" :model="form" :rules="form_rules" class="demo-form-inline">
                <el-form-item label="">
                  <el-input placeholder="" v-model="form.nickName"></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input placeholder="" v-model="form.phone"></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input placeholder="" v-model="form.roleName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="status">
                  <el-select v-model="form.status">
                    <el-option label="" :value=0></el-option>
                    <el-option label="" :value=1></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="onSubmit"></el-button>
                  <el-button @click="resetForm('form')"></el-button>
                </el-form-item>
              </el-form>
              <el-button type="primary" @click="show(null, '')"></el-button>
            </div>
            <el-table
              height="calc(100vh - 100px - 100px - 100px - 100px)"
              :data="resultList" v-if="resultList.length !== 0" @row-click="rowClick"
              :row-class-name="tableRowClassName"
              :header-cell-style="{background:'#ccc', color: '#fff'}"
              style="width: 100%; margin-top: 20px">
              <el-table-column align="center" label="" width="50">
                <template scope="scope">{{ scope.$index + 1 }}</template>
              </el-table-column>
              <el-table-column align="center" prop="nickName" label="">
              </el-table-column>
              <el-table-column align="center" prop="username" label=""></el-table-column>
              <el-table-column align="center" prop="phone" label="">
              </el-table-column>
              <el-table-column
                align="center"
                prop="department"
                label=""
              >
                <template scope="scope">{{ scope.row.sysDept.deptName }}</template>
              </el-table-column>
              <el-table-column
                align="center"
                prop="department"
                label=""
              >
                <template scope="scope">{{ scope.row.sysRole.roleName }}</template>
              </el-table-column>
              <el-table-column
                align="center"
                prop="createTime"
                label=""
                show-overflow-tooltip
              >
              </el-table-column>
              <el-table-column prop="starts" label="" align="center">
                <template scope="scope">
                  <p>
                    <a v-if="scope.row.status === 0"></a>
                    <a v-else></a>
                  </p>
                </template>
              </el-table-column>
              <el-table-column prop="starts" label="" align="center" fixed="right" min-width="200px">
                <template scope="scope">
                  <div class="operate">
                    <a @click="show(scope.row, '')" class="link-item"></a>
                    <el-tooltip class="item" effect="dark" content="123456" placement="top">
                      <a @click="resetPad(scope.row)" class="link-item"></a>
                    </el-tooltip>
                    <template>
                      <el-popconfirm
                        title=""
                        @confirm="show(scope.row, '')"
                      >
                        <el-link slot="reference" class="link-item"></el-link>
                      </el-popconfirm>
                    </template>
                  </div>
                </template>
              </el-table-column>
            </el-table>
            <my-empty v-else />
            <!--
                  
              -->
            <el-dialog
              :width="$store.state.dialogWidth"
              :destroy-on-close="true"
              :title="title"
              :visible.sync="isShow"
              :close-on-click-modal="false"
            >
              <el-form ref="addForm" :model="addForm" id="form" :rules="rules">
                <div class="width">
                  <el-form-item
                    class="form-item"
                    label=""
                    :label-width="formLabelWidth"
                    prop="nickName"
                  >
                    <el-input placeholder="" v-model="addForm.nickName" autocomplete="off"></el-input>
                  </el-form-item>
                  <el-form-item
                    class="form-item"
                    label=""
                    :label-width="formLabelWidth"
                    prop="roleId"
                  >
                    <el-select
                      v-if="title === ''"
                      style="width: 100%"
                      v-model="addForm.roleId "
                      placeholder=""
                    >
                      <el-option
                        :label="item.roleName"
                        :value="item.roleId"
                        v-for="item in roleList"
                      ></el-option>
                    </el-select>
                    <el-select
                      v-else
                      style="width: 100%"
                      class="form-item"
                      v-model="addForm.roleId "
                      :placeholder="addForm.department"
                    >
                      <el-option
                        :label="item.roleName"
                        :value="item.roleId"
                        v-for="item in roleList"
                      ></el-option>
                    </el-select>
                  </el-form-item>

                </div>
                <div class="width">
                  <el-form-item
                    class="form-item"
                    label=""
                    prop="deptId"
                    placeholder=""
                    :label-width="formLabelWidth"
                  >
                    <!--                  <my-tree ref="myTree" style="width: 100%" :options="treeArr" @getValue="getSelectedValue"></my-tree>-->
                    <treeselect v-model="addForm.deptId" :multiple="false" :options="treeArr"
                                :normalizer="normalizer" />
                  </el-form-item>
                  <el-form-item
                    class="form-item"
                    label=""
                    :label-width="formLabelWidth"
                    prop="sex"
                  >
                    <el-select v-model="addForm.sex" placeholder="" style="width: 100%">
                      <el-option
                        :label="item.label"
                        :value="item.value"
                        v-for="item in sexList"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </div>
                <div class="width">
                  <el-form-item
                    label=""
                    :label-width="formLabelWidth"
                    prop="username"
                  >
                    <el-input v-model="addForm.username" autocomplete="off">
                    </el-input>
                  </el-form-item>
                  <el-form-item label="" :label-width="formLabelWidth" prop="status">
                    <el-radio-group v-model="addForm.status">
                      <el-radio :label="0"></el-radio>
                      <el-radio :label="1"></el-radio>
                    </el-radio-group>
                  </el-form-item>
                </div>
                <div class="width">
                  <el-form-item
                    class="form-item"
                    label=""
                    prop="phone"
                    :label-width="formLabelWidth"
                  >
                    <el-input placeholder="" v-model="addForm.phone" autocomplete="off"></el-input>
                  </el-form-item>
                  <el-form-item
                    v-if="title !== ''"
                    class="form-item"
                    label=""
                    :label-width="formLabelWidth"
                    prop="password"
                  >
                    <el-input
                      placeholder=""
                      v-model="addForm.password"
                      autocomplete="off"
                    ></el-input>
                  </el-form-item>
                </div>
                <div class="width">
                  <el-form-item
                    class="form-item"
                    label=""
                    :label-width="formLabelWidth"
                    prop="email"
                  >
                    <el-input
                      placeholder=""
                      type="email"
                      v-model="addForm.email"
                      autocomplete="off"
                    ></el-input>
                  </el-form-item>
                </div>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="isShow = false"> </el-button>
                <el-button type="primary" @click="addForms('addForm')"
                > 
                </el-button
                >
              </div>
            </el-dialog>
          </div>
        </div>
      </el-row>
    </div>
    <my-footer v-on:next="next" @prev="prev" :form="form" @change="change" @pageCheng="pageCheng"></my-footer>

  </div>


</template>

<script>
import eTree from "@/components/eTree";
import {
  addUser,
  deleteUser,
  getOrganizeList,
  getRoleList,
  getUserAll,
  upDataPassword,
  upDataUser
} from "@/newwork/system-colltroner";
import { fn } from "@/uti";
import myEmpty from "@/newwork/myEmpty";
import { isValidPhone } from "@/util/validate";
import myTree from "@/components/myTree";
import myFooter from "@/components/myFooter";
import myElHeader from "@/components/myElHeader";
import treeselect from "@riophae/vue-treeselect";
// import the styles
import "@riophae/vue-treeselect/dist/vue-treeselect.css";

export default {
  name: "serve-manage",
  components: {
    eTree,
    myEmpty,
    myTree,
    myFooter,
    myElHeader,
    treeselect
  },
  data() {
    const validatePhone = (rule, value, callback) => {
      if (!isValidPhone(value)) {
        callback(new Error(","));
      } else {
        callback();
      }
    };

    return {
      normalizer(node) {
        if (node.children && !node.children.length) {
          delete node.children;
        }
        return {
          id: node.deptId,
          children: node.children,
          label: node.deptName
        };
      },
      form: {
        nickName: "",
        phone: "",
        status: "",
        pageNum: 1,
        pageSize: 10
      },
      addForm: {
        deptId: null,//
        email: "", //email
        nickName: "",//
        password: "",//
        phone: "", //
        roleId: "", // 'id'
        sex: "",  // / 
        status: 0,//
        pageSize: 10,
        pageNum: 1,
        username: "" //
      },
      row: {},
      type: "",
      title: "",
      deptIdList: [],
      isShow: false,
      resultList: [],
      departmentList: [],
      deptId: "",
      formLabelWidth: "120px",
      roleList: [],
      timer: {},
      treeArr: [],
      sexList: [
        { label: "", value: 0 },
        { label: "", value: 1 }
      ],
      rules: {
        nickName: [
          { required: true, message: "", trigger: "blur" }
        ],
        roleId: [
          { required: true, message: "", trigger: "blur" }
        ],
        email: [
          { required: false, message: "", trigger: "blur" }
          // {validator: validateEmail, message: ',', trigger: 'change'}
        ],
        deptId: [
          { required: true, message: "", trigger: "blur" }
        ],
        username: [
          { required: true, message: "", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" }
        ],

        phone: [
          { required: false, message: "", trigger: "blur" }
          // {validator: validatePhone, message: ',', trigger: 'change'}
        ],
        status: [
          { required: false, message: "", trigger: "blur" }
        ],
        sex: [
          { required: false, message: "", trigger: "blur" }
        ]
      },
      form_rules: {
        nickName: [
          { required: false, message: "", trigger: "blur" }
        ],
        phone: [
          { required: false, message: "", trigger: "blur" },
          { validator: validatePhone, message: ",", trigger: "blur" }
        ],
        status: [
          { required: false, message: "", trigger: "blur" }
        ]
      }

    };
  },

  methods: {
    getSelectedValue(value) {
      this.addForm.deptId = value.deptId;
    },
    onSubmit() {
      this.getUserAll(this.form);
    },
    treeClick(a) {
      this.form = this.$options.data().form;
      console.log(a);
      console.log(a);
      a.deptId === "1" ? this.form.deptId = "" : this.form.deptId = a.deptId;
      console.log(this.form);
      this.getUserAll(this.form);
    },
    next() {
      this.form.pageNum++;
      console.log(this.form);
      this.getUserAll(this.form);
    },
    prev() {
      this.form.pageNum--;
      this.getUserAll(this.form);
    },
    change(e) {
      console.log(e);
      this.form.pageNum = e;
      this.getUserAll(this.form);
    },
    reset(row) {
      const data = {};
      data.password = "123456";
      data.userId = row.userId;
      upDataPassword(data).then(res => {
        if (res.data.code === 200) {
          this.$message.success("");
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },

    addForms() {
      //  
      this.$refs.addForm.validate((valid) => {
        if (valid && this.addForm.deptId.length !== 0) {
          if (this.title === "") {
            addUser(this.addForm).then(res => {
              if (res.data.code === 200) {
                this.isShow = false;
                this.getUserAll(this.form);
                this.$message.success("");
              } else {
                this.$message.error(res.data.msg);
              }
            }).catch(e => {
              this.$message.error(e);
            });
          } else {
            upDataUser(this.addForm).then(res => {
              if (res.data.code === 200) {
                this.isShow = false;
                this.getUserAll(this.form);
                this.$message.success("");

                // 
                const userInfo = this.$store.state.userInfo;
                if (this.addForm.userId === userInfo.userId) {
                  this.$store.dispatch("userInfo", this.addForm);
                }
              } else {
                this.$message.error(res.data.msg);
              }
            });
          }
        } else {
          return false;
        }
      });
    },
    resetPad(row) {
      const data = {};
      data.userId = row.userId;
      console.log(row);
      data.password = "123456";
      upDataPassword(data).then(res => {
        console.log(res);
        if (res.data.code === 200) {
          this.$message.success("");
        } else {
          this.$message.error(res.data.msg);
        }
      }).catch(e => this.$message.error(e));
    },
    pageCheng(data) {
      this.form.pageSize = data;
      this.getUserAll(this.form);
    },
    onClear() {
      this.form.deptId = "";
      this.resetForm();
      this.getUserAll(this.form);
    },

    show(row, title) {
      getOrganizeList(this.form).then(res => {
        this.deptIdList = res.data.data;
      }).catch(e => {
        this.$message.error(e);
      });
      getRoleList().then(res => {
        console.log(res);
        if (res.data.code === 200) {
          this.roleList = res.data.data.records;
        }
      });
      this.title = title;
      if (this.title === "") {
        this.isShow = true;
        this.addForm = this.$options.data().addForm;
      } else if (title === "") {
        this.addForm = row;
        this.isShow = true;
      } else if (title === "") {
        deleteUser(row.userId).then(res => {
          if (res.data.code === 200) {
            this.$message.success("!");
            this.getUserAll(this.$store.state.formPage);
          } else {
            this.$message.error(res.data.msg);
          }
        }).catch(e => {
          this.$message.error(e);
        });
      }
    },
    resetForm(form) {
      if (form === "form") {
        this.form = this.$options.data().form;
        this.form.deptId = "";
      } else {
        this.$refs.addForm.resetFields();
      }
      this.getUserAll(this.form);
    },

    rowClick(row) {
      this.row = row;
    },
    tableRowClassName({ row, rowIndex }) {
      //row
      row.index = rowIndex;
    },
    getUserAll(formPage) {
      getUserAll(formPage).then(res => {
        this.$bus.$emit("total", res.data.data.total);
        this.resultList = res.data.data.records;
      });
    }
  },
  created() {
    this.getUserAll(this.form);
    getOrganizeList().then(res => {
      this.treeArr = fn(res.data.data);
    });
  },
  computed: {
    getterDeptIdList() {
      return fn(this.deptIdList);
    },
    defaultProps() {
      return {
        children: "children",
        label: "deptName",
        value: "deptId",
        expandTrigger: "hover"
      };
    }
  },


  watch: {
    timer(val) {
      this.addForm.createTime = val[0];
      this.addForm.endTime = val[1];
    },
    isShow(val, newVal) {
      if (!val) {
        this.addForm = this.$options.data().addForm;
      }
    }
  }
};
</script>

<style scoped>
#my-manage {
  display: flex;
  height: calc(100vh - 160px);
  justify-content: space-between;
  flex-direction: column;
}

.main {
  overflow: hidden;
  display: flex;
}

.container-l {
  width: 200px;
  flex-shrink: 0;
}

.container-r {
  width: calc(100vw - 200px - 90px - 200px);
}

.container-form {
  display: flex;
  height: 40px;
  justify-content: space-between;
}

#form {
  display: flex;
  flex-wrap: wrap;
}

.link-item {
  margin-right: 20px;
  cursor: pointer;
}

.form-item {
  width: 48%;
}

.err {
  color: red;
}

</style>
