<template>
  <div class="warps">
    <my-el-header />
    <div class="container">
      <div class="form-nav">
        <el-form :inline="true" :model="form" class="demo-form-inline">
          <el-form-item label="">
            <el-input v-model="form.user"></el-input>
          </el-form-item>
          <el-form-item label="IP">
            <el-input v-model="form.ip" />
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click=""></el-button>
            <el-button></el-button>
          </el-form-item>
        </el-form>
        <div class="nav-btn">
          <el-button
            type="primary"
            @click="showAddForm(null, '')"
          >{{ title }}
          </el-button
          >
        </div>
      </div>
      <el-table :data="list" style="width: 100%">
        <el-table-column prop="index" label="" width="180">
          <template scope="scope">
            {{ scope.$index + 1 }}
          </template>
        </el-table-column>
        <el-table-column prop="name" label="" width="200">
        </el-table-column>
        <el-table-column prop="ip" label="IP" width="200">
        </el-table-column>
        <el-table-column prop="ip" label="" width="300">
          <template scope="scope"
          >{{ scope.row.area[0] }}{{ scope.row.area[1] }}
          </template
          >
        </el-table-column>
        <el-table-column prop="SIP" label="SIP" width="300">
        </el-table-column>
        <el-table-column prop="address" label="">
          <template scope="scope">
            <el-link
              class="a-link"
              type="info"
              @click="showAddForm(scope.row, '')"
            >
            </el-link
            >

            <el-link class="a-link" type="info" @click="removeIt(scope.row)"
            >
            </el-link
            >

            <el-link
              class="a-link"
              type="info"
              @click="showAddForm(scope.row, '')"
            >
            </el-link
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog :width="$store.state.dialogWidth" :title="title" :visible.sync="dialogFormVisible">
      <el-form :model="addForm" ref="addFrom" :rules="rules">
        <div class="width">
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            prop="name"
          >
            <el-input v-model="addForm.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item
            label="SIP"
            :label-width="formLabelWidth"
            prop="SIP"
          >
            <el-input v-model="addForm.SIP" autocomplete="off"></el-input>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item label="IP" :label-width="formLabelWidth" prop="ip">
            <el-input v-model="addForm.ip" autocomplete="off"></el-input>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="addForm.host" autocomplete="off"></el-input>
          </el-form-item>
        </div>
        <div class="width">
          <el-form-item
            label=""
            :label-width="formLabelWidth"
            prop="area"
          >
            <el-cascader
              v-model="addForm.area"
              :options="options"
              :props="{ expandTrigger: 'hover' }"
            ></el-cascader>
          </el-form-item>
        </div>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.remark" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm('addForm')"
        > 
        </el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import eTree from "../../components/eTree.vue";
import myElHeader from "@/components/myElHeader";

export default {
  name: "terminal",
  components: {
    eTree,
    myElHeader
  },
  data() {
    return {
      formLabelWidth: "120px",
      dialogFormVisible: false,
      title: "",

      form: {},
      list: [],
      addForm: {
        name: "",
        ip: "",
        host: "",
        area: "",
        remark: ""
      },
      options: [
        {
          label: "",
          value: "",
          children: [
            { label: "", value: "" },
            { label: "", value: "" },
            { label: "", value: "" },
            { label: "", value: "" }
          ]
        }
      ],
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        ip: [{ required: true, message: "IP", trigger: "blur" }],
        area: [{ required: true, message: "", trigger: "blur" }],
        SIP: [{ required: true, message: "SIP", trigger: "blur" }]

      }
    };
  },
  methods: {
    submitForm(addForm) {
      this.$refs["addFrom"].validate((valid) => {
        if (valid) {
          this.list.push(this.addForm);
          this.$message({
            message: "",
            type: "success"
          });
          this.dialogFormVisible = false;
          window.localStorage.setItem("terminal", JSON.stringify(this.list));
        } else {
          this.$message({
            message: " ",
            type: "error"
          });
          return false;
        }
      });
    },
    showAddForm(row, title) {
      this.dialogFormVisible = true;
      this.title = title;
      if (title === "") {
        this.addForm = row;
      } else if (title === "") {
        this.addForm = row;
        this.isReadOnly = true;
      } else {
        this.resetForm();
      }
    },
    tableRowClassName({ row, rowIndex }) {
      row.index = rowIndex;
    },

    removeIt(row) {
      this.list.map((item, i) => {
        if (row === item) {
          this.list.splice(i, 1);
        }
      });
    }
  },
  created() {
    this.list = JSON.parse(window.localStorage.getItem("terminal")) || [];
  }
};
</script>

<style scoped>
.width {
  width: 50%;
}

.a-link {
  margin-right: 20px;
}
</style>
