<template>
  <div class="warps">
    <my-el-header />
    <div class="container" id="networkManagement">
      <div class="nav-form">
        <el-form :model="forms" :hide-required-asterisk="true" :show-message="false" :inline="true"
                 class="demo-form-inline" ref="form" :rules="rule">
          <el-form-item :span="5" label="" prop="gatewayName">
            <el-input placeholder="" v-model="forms.gatewayName"></el-input>
          </el-form-item>
          <el-form-item :span="1000000" label="IP" prop="gatewayIp">
            <el-input placeholder="" v-model="forms.gatewayIp"></el-input>
          </el-form-item>
          <el-form-item label="" prop="gatewayType">
            <el-select v-model="forms.gatewayType" placeholder="">
              <el-option
                :label="item.label"
                :value="item.value"
                v-for="item in relayType"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="find"></el-button>
            <el-button @click="clear()"></el-button>
          </el-form-item>
        </el-form>
        <el-button type="primary" @click="showAddForm(null, '')"
        >
        </el-button
        >
      </div>
      <el-dialog :width="$store.state.dialogWidth" :close-on-click-modal="false" :title="title"
                 :visible.sync="dialogFormVisible" destroy-on-close>
        <el-form :model="addFrom" ref="formName" :rules="rules">
          <div class="width">
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="gatewayName"
            >
              <el-input placeholder="" v-model="addFrom.gatewayName" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="gatewayType"
            >
              <el-select v-model="addFrom.gatewayType" placeholder="" style="width: 100%">
                <el-option
                  :label="item.label"
                  :value="item.value"
                  v-for="item in relayType"
                ></el-option>
              </el-select>
            </el-form-item>
          </div>
          <div class="width">
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="realm"
            >
              <el-input placeholder=""
                        v-model="addFrom.realm"
                        autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="proxyIp"
            >
              <el-input placeholder=""
                        v-model="addFrom.proxyIp"
                        autocomplete="off"
              ></el-input>
            </el-form-item>
          </div>
          <div class="width">
            <el-form-item
              v-if="title === ''"
              label=""
              :label-width="formLabelWidth"
              prop="codecs"
            >
              <el-select v-model="codecs"
                         @change="codecsBlur"
                         multiple
                         style="width: 100%"
                         collapse-tags>
                <el-option
                  v-for="item in dictionaryList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.name">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              required
            >
              <el-select v-model="addFrom.dtmfMode" style="width: 100%">
                <el-option
                  v-for="item in dtmfModeData"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </div>
          <div class="width">
            <el-form-item
              v-if="title === ''"
              label=""
              :label-width="formLabelWidth"
              prop="expires"
            >
              <el-input placeholder=""
                        v-model="addFrom.expires"
                        autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label="IP"
              :label-width="formLabelWidth"
              prop="gatewayIp"
            >
              <el-input placeholder=""
                        v-model="addFrom.gatewayIp"
                        autocomplete="off"
              ></el-input>
            </el-form-item>
          </div>
          <div class="width">
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="gatewayPort"
            >
              <el-input placeholder=""
                        v-model="addFrom.gatewayPort"
                        autocomplete="off"
              ></el-input>
            </el-form-item>
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="profileId"
            >
              <el-select v-model="addFrom.profileId" placeholder="" style="width: 100%">
                <el-option
                  v-for="item in profileIdList"
                  :key="item.id"
                  :label="item.netName"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </div>
          <div class="width">
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="maxCall"
            >
              <el-input placeholder="" v-model="addFrom.maxCall" />
            </el-form-item>
            <el-form-item prop="accountUser" label="" :label-width="formLabelWidth">
              <el-input :disabled="addFrom.gatewayType === 1" placeholder=""
                        v-model="addFrom.accountUser"></el-input>
            </el-form-item>
          </div>
          <div v-if="addFrom.register === 1" class="width">
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="username"
            >
              <el-input v-model="addFrom.username" placeholder="" />
            </el-form-item>
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="password"
            >
              <el-input v-model="addFrom.password" placeholder="" />
            </el-form-item>
          </div>

          <div class="width">
            <el-form-item
              label=""
              :label-width="formLabelWidth"
              prop="register"
            >
              <el-radio-group v-model="addFrom.register">
                <el-radio :label="1"></el-radio>
                <el-radio :label="0"></el-radio>
              </el-radio-group>
            </el-form-item>
          </div>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="submitForm('addFrom')"
          > 
          </el-button
          >
        </div>
      </el-dialog>

      <div class="table-warp">
        <div class="table">
          <el-table :header-cell-style="{background:'#ccc', color: '#fff',}"
                    height="calc(100vh - 100px - 100px - 100px - 100px)" :data="list" style="width: 100%">
            <el-table-column align="center" prop="date" label="" width="50px">
              <template scope="scope">{{ scope.$index + 1 }}</template>
            </el-table-column>
            <el-table-column align="center" prop="gatewayName" label=""></el-table-column>
            <el-table-column align="center" prop="accountUser" label=""></el-table-column>
            <el-table-column align="center" prop="expires" label="(s)"></el-table-column>
            <el-table-column align="center" prop="gatewayIp" label="IP"></el-table-column>
            <el-table-column align="center" prop="gatewayPort" label=""></el-table-column>
            <el-table-column align="center" prop="gatewayType" label="">
              <template scope="scope">
                <a v-if="scope.row.gatewayType === 0"></a>
                <a v-else-if="scope.row.gatewayType === 1"></a>
                <a v-else-if="scope.row.gatewayType === 2"></a>
                <a v-else></a>
              </template>
            </el-table-column>
            <el-table-column align="center" prop="username" label="">
              <template scope="scope">
                <div v-if="scope.row.register === 1"> </div>
                <div v-else> </div>
              </template>
            </el-table-column>
            <el-table-column prop="recording" align="center" fixed="right" label="" min-width="100px">
              <template scope="scope">
                <div class="operate">
                  <el-link
                    @click="showAddForm(scope.row, '')"
                    type="info"
                  >
                  </el-link
                  >
                  <template>
                    <el-popconfirm title="" @confirm="removeIt(scope.row)">
                      <el-link type="info" slot="reference">
                      </el-link>
                    </el-popconfirm>
                  </template>
                </div>
              </template>
            </el-table-column>
          </el-table>

        </div>
      </div>

    </div>
    <my-footer v-on:next="next" @prev="prev" :form="forms" @change="change" @pageCheng="pageCheng"></my-footer>
  </div>

</template>

<script>
import { deletePbx, getPbxAdd, getPbxAll, getProfileInfo, upDatePbx } from "@/newwork/ground-colltroner";
import { addDictionaryList, getDictionaryAll } from "@/newwork/system-colltroner";
import { isValidIP, isValidNumber, isValidPost } from "@/util/validate";
import myFooter from "@/components/myFooter";
import myElHeader from "@/components/myElHeader";


export default {
  name: "networkManagement",
  data() {

    /**
     *
     * ip
     * @param rule  
     * @param value 
     * @param callback  
     */
    const validateIP = (rule, value, callback) => {
      if (!isValidIP(value)) {
        callback(new Error("ip,"));
      } else {
        callback();
      }
    };
    const validatePost = (rule, value, callback) => {
      if (!isValidPost(value)) {
        callback(new Error("ip,"));
      } else {
        callback();
      }
    };
    const validateNum = (rule, value, callback) => {
      if (!isValidNumber(value)) {
        callback(new Error("ip,"));
      } else {
        callback();
      }
    };
    return {
      dialogFormVisible: false,
      formLabelWidth: "120px",
      title: "",
      forms: {},
      list: [],
      dtmfModeData: [],
      dictionaryList: [],//''
      profileIdList: [], // ID
      codecs: [],
      addFrom: {
        accountUser: "", //
        codecs: "", //
        dtmfMode: "", //dtmfMode
        expires: "", //
        gatewayIp: "", //IP
        gatewayName: "", //
        gatewayPort: "", //
        gatewayType: "", //
        maxCall: "", //
        password: "", //
        profileId: "", //profileId
        proxyIp: "", //IP
        realm: "", //
        register: 1, // 0/ 1 
        username: "" //
      },
      codecsList: [],
      relayType: [
        { label: "", value: 0 },
        { label: "", value: 1 },
        { label: "", value: 2 }
      ],
      networkStat: [
        { label: "", value: "" },
        { label: "", value: "" }
      ],
      registerTypr: [
        { label: "", value: 1 },
        { label: "", value: 0 }
      ],
      rule: {
        gatewayName: [
          { required: false, message: " ", trigger: "blur" }
        ],
        gatewayIp: [
          { required: false, message: " ", trigger: "blur" }
        ],
        gatewayType: [
          { required: false, message: " ", trigger: "blur" }
        ]
      },
      rules: {
        accountUser: [{ required: false, message: " ", trigger: "blur" }],
        codecs: [
          { required: true, message: " ", trigger: "blur" }
        ],
        dtmfMode: [
          { required: true, message: " ", trigger: "blur" }
        ],
        expires: [
          { required: true, message: " ", trigger: "blur" },
          { validator: validateNum, message: "", trigger: "blur" }
        ],
        gatewayIp: [
          { required: true, message: " ", trigger: "blur" },
          { validator: validateIP, message: "IP, ", trigger: "blur" }
        ],
        gatewayName: [
          { required: true, message: " ", trigger: "blur" }
        ],
        gatewayPort: [{ required: true, message: " ", trigger: "blur" },
          { validator: validatePost, message: ", ", trigger: "blur" }
        ],
        gatewayType: [
          { required: true, message: " ", trigger: "blur" }
        ],
        maxCall: [{ required: true, message: " ", trigger: "blur" }],
        password: [
          { required: true, message: " ", trigger: "blur" }
        ],
        profileId: [
          { required: true, message: " ", trigger: "blur" }
        ],
        proxyIp: [{ required: true, message: " ", trigger: "blur" },
          { validator: validateIP, message: "IP, ", trigger: "blur" }],
        realm: [{ required: true, message: " ", trigger: "blur" }],
        register: [{ required: true, message: " ", trigger: "blur" }],
        username: [{ required: true, message: " ", trigger: "blur" }]
      }
    };
  },
  components: {
    myFooter,
    myElHeader
  },
  methods: {
    next() {
      this.forms.pageNum++;
      this.getPbxAll(this.forms);
    },
    prev() {
      this.forms.pageNum--;
      this.getPbxAll(this.forms);
    },
    change(e) {
      this.forms.pageNum = e;
      this.getPbxAll(this.forms);
    },
    pageCheng(e) {
      this.forms = this.$options.data().forms;
      this.forms.pageSize = e;
      this.getPbxAll(this.forms);
    },
    clear() {
      this.forms = this.$options.data().forms;
      this.getPbxAll(this.forms);
    },
    find() {
      this.getPbxAll(this.forms);
    },

    getPbxAll(form) {
      getPbxAll(form).then(res => {
        if (res.data.code === 200) {
          this.$bus.$emit("total", res.data.data.total);
          this.list = res.data.data.records;
        }
      }).catch(e => {
        this.$message.error(e);
      });
    },
    submitForm() {
      this.$refs["formName"].validate((valid) => {
        if (valid) {
          if (this.title === "") {
            getPbxAdd(this.addFrom).then(res => {
              if (res.data.code === 200) {
                this.$message.success("");
                this.getPbxAll(this.from);
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.data.msg);
              }
            });
          } else {
            upDatePbx(this.addFrom).then(res => {
              if (res.data.code === 200) {
                this.$message.success("");
                this.getPbxAll(this.from);
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.data.msg);
              }
            });
          }
          // this.resetForm('formName')
        } else {
          console.log("error submit!!");
        }
      });
    },
    resetForm(type) {
      type === "form" ? this.$refs["form"].resetFields() : this.$refs["formName"].resetFields();
    },
    showAddForm(row, title) {
      this.dialogFormVisible = true;
      this.title = title;
      if (title === "") {
        this.addFrom = row;
      } else if (title === "") {
        this.addForm = row;
        this.isReadOnly = true;
      } else {
        // this.resetForm("formName")
      }
    },
    tableRowClassName({ row, rowIndex }) {
      row.index = rowIndex;
    },

    removeIt(row) {
      deletePbx(row.id).then(res => {
        if (res.data.code === 200) {
          this.$message.success("");
          this.getPbxAll(this.forms);
        } else {
          this.$message.error(res.data.msg);
        }
      }).catch(e => {
        this.$message.error(e);
      });
    },
    getProfileInfo() {
      getProfileInfo().then(res => {
        if (res.data.code === 200) {
          this.profileIdList = res.data.data.records;
        }
      });
    },
    codecsBlur() {
      this.addFrom.codecs = this.codecs.join(",");
    },
    /**
     * , code
     *
     * @param dictionaryTypeList
     */
    filterGateway(dictionaryTypeList) {
      let _data = {};
      let _dtmfModeData = {};

      dictionaryTypeList.forEach(item => {
        if (item.name === "") {
          _data.id = item.id;
          _data.code = item.code;
          getDictionaryAll(_data).then(res => {
            this.dictionaryList = res.data.data;
          });
        }
        if (item.name === "") {
          _dtmfModeData.code = item.code;
          getDictionaryAll(_dtmfModeData).then(res => {
            this.dtmfModeData = res.data.data;
          });
        }
      });
    }
  },
  created() {

    this.getPbxAll(this.forms);
    this.getProfileInfo();
  },
  watch: {
    dialogFormVisible(val) {
      if (val === false) {
        this.addFrom = this.$options.data().addFrom;
      } else {
        let dictionaryTypeList = [];
        addDictionaryList().then(res => {
          dictionaryTypeList = res.data.data.records;
          //ID
          this.filterGateway(dictionaryTypeList);
        });
      }
    }
  }
};
</script>

<style>

#networkManagement .container p {
  background-color: #f2f2f2;
  padding: 10px 15px;
}

.nav-form {
  margin: 15px;
  height: 40px;
  display: flex;
  overflow: hidden;
  justify-content: space-between;
}

.nav-form form {
  height: 40px;
}

#networkManagement .width {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

#networkManagement .width > div {
  flex: 1;
}

#networkManagement .operate {
  padding: 0;
}
</style>
